<template>
  <v-app>
    <div class="bodyBox">
      <div class="loginForm">
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
          <v-row>
            <v-col>
              <v-text-field
                v-model="id"
                :rules="idRules"
                label="ID"
                required
                filled
                rounded
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                v-model="pass"
                :rules="rules"
                class="input-group--focused"
                name="input-10-2"
                value=""
                :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show2 ? 'text' : 'password'"
                @click:append="show2 = !show2"
                label="PASS"
                required
                filled
                rounded
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-btn
            :disabled="!valid"
            color="success"
            class="mr-4"
            @click="validate"
            filled
            rounded
            dense
            type="submit"
          >
            Validate
          </v-btn>

          <v-btn
            color="error"
            class="mr-4"
            @click="reset"
            filled
            rounded
            dense
          >
            Reset Form
          </v-btn>
        </v-form>
      </div>
    </div>
  </v-app>
</template>

<script>
  export default {
    name: 'loginApp',
    data: () => {
      return {
        id: '',
        idRules: [
          v => !!v || 'ID is required',
          v => /^[a-zA-Zㄱ-힣0-9]*$/.test(v) || 'Special characters cannot be entered',
        ],
        pass: '',
        rules: [
          v => !!v || 'Required.',
          v => v.length >= 8 || 'Min 8 characters',
        ],
        show2: false,
      }
    },
    methods: {
      validate () {
        this.$axios.post('/api/data', { ok: 'ok' }).then(res => {
          console.log(res.data)
        })
      },
      reset () {
        this.$refs.form.reset()
      },
    },
  }
</script>

